import { i18n } from "src/i18n";
import Image from "../shared/Image";
import { useEffect } from "react";
// publications
import selectorsCategory from "src/modules/categoryPublication/list/categoryPublicationListSelectors";
import actionsCategory from "src/modules/categoryPublication/list/categoryPublicationListActions";
import { Link } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";
import { baseUrl } from "src/view/shared/BaseUrl";
import moment from "moment";

function PublicationsComponent() {
  const dispatch = useDispatch();
  const langue = localStorage.getItem("language");
  useEffect(() => {
    dispatch(actionsCategory.doFetch());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const Substring = (text, limit) => {
    if (text) {
      if (text.length > limit) {
        return text.substring(0, limit) + "...";
      } else return text;
    } else return text;
  };
  const selectRowsCategory = useSelector(selectorsCategory.selectRows);

  const renderPublications = (i) => {
    return (
      <div>
        <Link to={`/detail/${selectRowsCategory[0].publications[i + 1].id}`}>
          {langue === "fr" ? (
            <Image
              className="lazyload"
              src={
                selectRowsCategory[0].publications[i + 1].photos.length > 0
                  ? `${baseUrl}/file/download?privateUrl=${
                      selectRowsCategory[0].publications[i + 1].photos[0]
                        .privateUrl
                    }`
                  : `https://placehold.jp/670x354.png`
              }
              alt="publication2 Icon"
            />
          ) : langue === "ar" ? (
            <Image
              className="lazyload"
              src={
                selectRowsCategory[0].publications[i + 1].photosAR.length > 0
                  ? `${baseUrl}/file/download?privateUrl=${
                      selectRowsCategory[0].publications[i + 1].photosAR[0]
                        .privateUrl
                    }`
                  : `https://placehold.jp/670x354.png`
              }
              alt="publication2 Icon"
            />
          ) : (
            <Image
              className="lazyload"
              src={
                selectRowsCategory[0].publications[i + 1].photosEN.length > 0
                  ? `${baseUrl}/file/download?privateUrl=${
                      selectRowsCategory[0].publications[i + 1].photosEN[0]
                        .privateUrl
                    }`
                  : `https://placehold.jp/670x354.png`
              }
              alt="publication2 Icon"
            />
          )}

          <div className="list__detaill">
            <div className="detaill__header">
              <div className="header__left">
                <p>
                  {langue === "ar"
                    ? Substring(
                        selectRowsCategory[0].publications[i + 1].thematique[0]
                          .titleAR,
                        30
                      )
                    : langue === "fr"
                    ? Substring(
                        selectRowsCategory[0].publications[i + 1].thematique[0]
                          .titleFR,
                        30
                      )
                    : Substring(
                        selectRowsCategory[0].publications[i + 1].thematique[0]
                          .titleEN,
                        30
                      )}
                </p>
              </div>
              <div className="header__right">
                {moment(selectRowsCategory[0].publications[i + 1].date).format(
                  "DD-MM-YYYY"
                )}
              </div>
            </div>
            <div className="detaill__content">
              {langue === "ar"
                ? Substring(
                    selectRowsCategory[0].publications[i + 1].titleAR,
                    30
                  )
                : langue === "fr"
                ? Substring(
                    selectRowsCategory[0].publications[i + 1].titleFR,
                    30
                  )
                : Substring(
                    selectRowsCategory[0].publications[i + 1].titleEN,
                    30
                  )}
            </div>
          </div>
        </Link>
      </div>
    );
  };
  return (
    <div>
      {selectRowsCategory.length > 0 ? (
        <div className="app__publications">
          <div className="publication">
            {selectRowsCategory[0] ? (
              <div className="publication__left">
                <div className="archieve__header">
                  <h2>
                    {langue === "ar"
                      ? selectRowsCategory[0].titleAR
                      : langue === "fr"
                      ? selectRowsCategory[0].titleFR
                      : selectRowsCategory[0].titleEN}
                  </h2>
                  <div className="publication__bar" />
                </div>
                {selectRowsCategory[0].publications.length > 0 ? (
                  <div className="publication__images">
                    <Link
                      to={`/detail/${selectRowsCategory[0].publications[0].id}`}
                    >
                      <div className="images__header">
                        <div className="__header">
                          {langue === "fr" ? (
                            <Image
                              className="lazyload"
                              src={
                                selectRowsCategory[0].publications[0].photos
                                  .length > 0
                                  ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[0].publications[0].photos[0].privateUrl}`
                                  : `https://placehold.jp/670x354.png`
                              }
                              alt="Publication Image"
                            />
                          ) : langue === "ar" ? (
                            <Image
                              className="lazyload"
                              src={
                                selectRowsCategory[0].publications[0].photosAR
                                  .length > 0
                                  ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[0].publications[0].photosAR[0].privateUrl}`
                                  : `https://placehold.jp/670x354.png`
                              }
                              alt="Publication Image"
                            />
                          ) : (
                            <Image
                              className="lazyload"
                              src={
                                selectRowsCategory[0].publications[0].photosEN
                                  .length > 0
                                  ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[0].publications[0].photosEN[0].privateUrl}`
                                  : `https://placehold.jp/670x354.png`
                              }
                              alt="Publication Image"
                            />
                          )}

                          <div className="__detaills">
                            <div className="list__detaill">
                              <div className="detaill__header">
                                <div className="__left">
                                  <p>
                                    {langue === "ar"
                                      ? selectRowsCategory[0].publications[0]
                                          .thematique[0].titleAR
                                      : langue === "fr"
                                      ? selectRowsCategory[0].publications[0]
                                          .thematique[0].titleFR
                                      : selectRowsCategory[0].publications[0]
                                          .thematique[0].titleEN}
                                  </p>
                                </div>
                                <div className="__right">
                                  {moment(
                                    selectRowsCategory[0].publications[0].date
                                  ).format("DD-MM-YYYY")}
                                </div>
                              </div>
                              <div className="__content">
                                {langue === "ar"
                                  ? Substring(
                                      selectRowsCategory[0].publications[0]
                                        .titleAR,
                                      30
                                    )
                                  : langue === "fr"
                                  ? Substring(
                                      selectRowsCategory[0].publications[0]
                                        .titleFR,
                                      30
                                    )
                                  : Substring(
                                      selectRowsCategory[0].publications[0]
                                        .titleEN,
                                      30
                                    )}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </Link>
                    {selectRowsCategory[0].publications.length > 0 ? (
                      <div className="images__bottom">
                        {Array.from({
                          length:
                            (selectRowsCategory[0].publications.length < 4
                              ? selectRowsCategory[0].publications.length
                              : 4) - 1,
                        }).map((item, index) => renderPublications(index))}
                      </div>
                    ) : null}
                  </div>
                ) : null}
                <Link to={`/detaill/category/${selectRowsCategory[0]._id}`}>
                  <div className="plus__button">{i18n("common.voirPlus")}</div>
                </Link>
              </div>
            ) : null}
            {selectRowsCategory.length > 1 ? (
              <div className="publication__right">
                <div className="communique">
                  <div className="archieve__header">
                    <h2>
                      {langue === "ar"
                        ? selectRowsCategory[1].titleAR
                        : langue === "fr"
                        ? selectRowsCategory[1].titleFR
                        : selectRowsCategory[1].titleEN}
                    </h2>
                    <div className="communiquer__bar" />
                  </div>
                  {selectRowsCategory[1].publications.length > 0 ? (
                    <>
                      {Array.from({
                        length:
                          selectRowsCategory[1].publications.length <= 2
                            ? selectRowsCategory[1].publications.length
                            : 2,
                      }).map((item, i) => (
                        <div className="images__header">
                          <div className="__header">
                            <Link
                              to={`/detail/${selectRowsCategory[1].publications[i].id}`}
                            >
                              {langue === "fr" ? (
                                <Image
                                  className="lazyload"
                                  src={
                                    selectRowsCategory[1].publications[i].photos
                                      .length > 0
                                      ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[1].publications[i].photos[0].privateUrl}`
                                      : `https://placehold.jp/426x248.png`
                                  }
                                  alt="Publication Image"
                                />
                              ) : langue === "ar" ? (
                                <Image
                                  className="lazyload"
                                  src={
                                    selectRowsCategory[1].publications[i]
                                      .photosAR.length > 0
                                      ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[1].publications[i].photosAR[0].privateUrl}`
                                      : `https://placehold.jp/426x248.png`
                                  }
                                  alt="Publication Image"
                                />
                              ) : (
                                <Image
                                  className="lazyload"
                                  src={
                                    selectRowsCategory[1].publications[i]
                                      .photosEN.length > 0
                                      ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[1].publications[i].photosEN[0].privateUrl}`
                                      : `https://placehold.jp/426x248.png`
                                  }
                                  alt="Publication Image"
                                />
                              )}

                              <div className="__detaills">
                                <div className="list__detaill">
                                  <div className="detaill__header">
                                    <div className="__left">
                                      <p>
                                        {langue === "fr"
                                          ? selectRowsCategory[1].publications[
                                              i
                                            ].thematique[0].titleFR
                                          : langue === "ar"
                                          ? selectRowsCategory[1].publications[
                                              i
                                            ].thematique[0].titleAR
                                          : selectRowsCategory[1].publications[
                                              i
                                            ].thematique[0].titleEN}
                                      </p>
                                    </div>
                                    <div className="__right">
                                      {" "}
                                      {moment(
                                        selectRowsCategory[1].publications[i]
                                          .date
                                      ).format("DD-MM-YYYY")}
                                    </div>
                                  </div>
                                  <div className="__content">
                                    {langue === "fr"
                                      ? Substring(
                                          selectRowsCategory[1].publications[i]
                                            .titleFR,
                                          30
                                        )
                                      : langue === "en"
                                      ? Substring(
                                          selectRowsCategory[1].publications[i]
                                            .titleEN,
                                          30
                                        )
                                      : Substring(
                                          selectRowsCategory[1].publications[i]
                                            .titleAR,
                                          30
                                        )}
                                  </div>
                                </div>
                              </div>
                            </Link>
                          </div>
                        </div>
                      ))}
                      <div className="communique">
                        <div className="">
                          <div className="publication__list">
                            {Array.from({
                              length:
                                (selectRowsCategory[1].publications.length < 6
                                  ? selectRowsCategory[1].publications.length
                                  : 6) - 2,
                            }).map((item, i) => (
                              <div>
                                <Link
                                  to={`/detail/${
                                    selectRowsCategory[1].publications[i + 2].id
                                  }`}
                                >
                                  {langue === "fr" ? (
                                    <Image
                                      className="lazyload"
                                      src={
                                        selectRowsCategory[1].publications[
                                          i + 2
                                        ].photos.length > 0
                                          ? `${baseUrl}/file/download?privateUrl=${
                                              selectRowsCategory[1]
                                                .publications[i + 2].photos[0]
                                                .privateUrl
                                            }`
                                          : `https://placehold.jp/120x80.png`
                                      }
                                      alt=""
                                      id="publication__leftImage"
                                    />
                                  ) : langue === "ar" ? (
                                    <Image
                                      className="lazyload"
                                      src={
                                        selectRowsCategory[1].publications[
                                          i + 2
                                        ].photosAR.length > 0
                                          ? `${baseUrl}/file/download?privateUrl=${
                                              selectRowsCategory[1]
                                                .publications[i + 2].photosAR[0]
                                                .privateUrl
                                            }`
                                          : `https://placehold.jp/120x80.png`
                                      }
                                      alt=""
                                      id="publication__leftImage"
                                    />
                                  ) : (
                                    <Image
                                      className="lazyload"
                                      src={
                                        selectRowsCategory[1].publications[
                                          i + 2
                                        ].photosEN.length > 0
                                          ? `${baseUrl}/file/download?privateUrl=${
                                              selectRowsCategory[1]
                                                .publications[i + 2].photosEN[0]
                                                .privateUrl
                                            }`
                                          : `https://placehold.jp/120x80.png`
                                      }
                                      alt=""
                                      id="publication__leftImage"
                                    />
                                  )}
                                </Link>
                                <Link
                                  to={`/detail/${
                                    selectRowsCategory[1].publications[i + 2].id
                                  }`}
                                >
                                  <div className="list__detaill">
                                    <div className="detaill__header">
                                      <div className="header__left">
                                        <p>
                                          {langue === "ar"
                                            ? selectRowsCategory[1]
                                                .publications[i + 2]
                                                .thematique[0].titleAR
                                            : langue === "fr"
                                            ? selectRowsCategory[1]
                                                .publications[i + 2]
                                                .thematique[0].titleFR
                                            : selectRowsCategory[1]
                                                .publications[i + 2]
                                                .thematique[0].titleEN}
                                        </p>
                                      </div>
                                      <div className="header__right">
                                        {moment(
                                          selectRowsCategory[1].publications[
                                            i + 2
                                          ].date
                                        ).format("DD-MM-YYYY")}
                                      </div>
                                    </div>
                                    <div className="detaill__content">
                                      {langue === "ar"
                                        ? Substring(
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].titleAR,
                                            30
                                          )
                                        : langue === "fr"
                                        ? Substring(
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].titleFR,
                                            30
                                          )
                                        : Substring(
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].titleEN,
                                            30
                                          )}
                                    </div>
                                  </div>
                                </Link>
                              </div>
                            ))}
                          </div>
                        </div>
                        <Link
                          to={`/detaill/category/${selectRowsCategory[1]._id}`}
                        >
                          <div className="plus__button">
                            {i18n("common.voirPlus")}
                          </div>
                        </Link>
                      </div>
                    </>
                  ) : null}
                </div>
              </div>
            ) : null}
          </div>
        </div>
      ) : null}
    </div>
  );
}

export default PublicationsComponent;
