import { baseUrl } from "src/view/shared/BaseUrl";
import moment from "moment";
import { Scheduler } from "devextreme-react/scheduler";
import actionsEvents from "src/modules/evenement/list/evenementListActions";
import selectorsvents from "src/modules/evenement/list/evenementListSelectors";
import { useDispatch, useSelector } from "react-redux";
import { bar, calendar, time_clock_circle } from "src/assets/images";

import { i18n } from "src/i18n";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";

function EventsComponent() {
  const currentDate = new Date();
  const dispatch = useDispatch();
  const history = useHistory();
  const langue = localStorage.getItem("language");
  var localization = require("devextreme/localization");
  localization.locale(langue);
  const [eventData, setEventData] = useState([
    {
      id: "",
      text: "",
      startDate: new Date(),
      endDate: new Date(),
      allDay: true,
      img: "",
      description: "",
    },
  ]);

  useEffect(() => {
    dispatch(actionsEvents.doFetch());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const rowsEvents = useSelector(selectorsvents.selectRows);

  const eventHeight =
    rowsEvents.length <= 3 ? 202 * rowsEvents.length + "px" : "606px";
  const eventGridTemplate =
    rowsEvents.length <= 3
      ? "202px".concat(
          " ",
          rowsEvents.length === 2
            ? "202px"
            : rowsEvents.length === 3
            ? "202px 202px"
            : ""
        )
      : "202px 202px 202px";
  const convertEventData = () => {
    const array = [
      {
        id: "",
        text: "",
        startDate: new Date(),
        endDate: new Date(),
        allDay: true,
        img: "",
        description: "",
      },
    ];
    rowsEvents.map((event) => {
      const data = {
        id: event.id,
        text:
          langue === "ar"
            ? event.titleAR
            : langue === "fr"
            ? event.titleFR
            : event.titleEN,
        description:
          langue === "ar"
            ? event.emplacementAR
            : langue === "fr"
            ? event.emplacementFR
            : event.emplacementEN,
        startDate: new Date(event.date),
        endDate: new Date(event.date),
        allDay: true,
        img:
          langue === "fr"
            ? event.photos.length > 0
              ? event.photos[0].downloadUrl
              : ""
            : langue === "ar"
            ? event.photosAR.length > 0
              ? event.photosAR[0].downloadUrl
              : ""
            : event.photosEN.length > 0
            ? event.photosEN[0].downloadUrl
            : "",
      };
      array.push(data);
    });
    array.shift();
    setEventData(array);
  };
  useEffect(() => {
    convertEventData();
  }, [rowsEvents]);
  const Substring = (text, limit) => {
    if (text) {
      if (text.length > limit) {
        return text.substring(0, limit) + "...";
      } else return text;
    } else return text;
  };
  const renderAppointmentTooltip = (model) => {
    return (
      <Link to={`/Evenements/${model.appointmentData.id}`}>
        <div className="evenement__right" style={{ height: "300" }}>
          <h5 style={{ fontSize: 20 }}>{model.appointmentData.text}</h5>
          <p>
            <img
              src={model.appointmentData.img}
              style={{ marginTop: 10, height: "150px" }}
            />
          </p>
          <div className="right__footer">
            {Substring(model.appointmentData.description, 150)}
          </div>
        </div>
      </Link>
    );
  };
  return (
    <>
      {rowsEvents.length > 0 ? (
        <div className="app__evenement">
          <div className="evenenement">
            <div className="archieve__header">
              <h2>{i18n("common.Evènements")}</h2>
              <div className="satestique__bar" />
            </div>
            <div
              className="evenement__content"
              style={{ width: "fit-content" }}
            >
              <div
                className="evenement__timing"
                style={{ height: eventHeight }}
              >
                <div className="timing__bar">
                  <img
                    className="lazyload"
                    src={bar}
                    alt=""
                    style={{ height: eventHeight }}
                  />
                </div>
                <div className="timing__number">
                  {Array.from({
                    length: rowsEvents.length < 3 ? rowsEvents.length : 3,
                  }).map((item, i) => (
                    <div>
                      <div className="cercle__red" />
                      <div className="time">
                        <div className="date">
                          {moment(rowsEvents[i].date).format("D")}
                        </div>
                        <div className="month">
                          {moment(rowsEvents[i].date)
                            .locale(langue === "ar" ? "ar" : "fr")
                            .format("MMMM")}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              <div
                className="evenement__description"
                style={{
                  gridTemplateRows: eventGridTemplate,
                }}
              >
                {Array.from({
                  length: rowsEvents.length < 3 ? rowsEvents.length : 3,
                }).map((item, i) => (
                  <div>
                    <div className="evenement__left">
                      <Link to={`/Evenements/${rowsEvents[i]._id}`}>
                        <span
                          className=" lazy-load-image-background blur lazy-load-image-loaded"
                          style={{ color: "transparent", display: "contents" }}
                        >
                          {langue === "fr" ? (
                            <img
                              alt="https://placehold.jp/204x175.png"
                              src={
                                rowsEvents[i].photos.length > 0
                                  ? `${baseUrl}/file/download?privateUrl=${rowsEvents[i].photos[0].privateUrl}`
                                  : `https://placehold.jp/204x175.png`
                              }
                            />
                          ) : langue === "ar" ? (
                            <img
                              alt="https://placehold.jp/204x175.png"
                              src={
                                rowsEvents[i].photosAR.length > 0
                                  ? `${baseUrl}/file/download?privateUrl=${rowsEvents[i].photosAR[0].privateUrl}`
                                  : `https://placehold.jp/204x175.png`
                              }
                            />
                          ) : (
                            <img
                              alt="https://placehold.jp/204x175.png"
                              src={
                                rowsEvents[i].photosEN.length > 0
                                  ? `${baseUrl}/file/download?privateUrl=${rowsEvents[i].photosEN[0].privateUrl}`
                                  : `https://placehold.jp/204x175.png`
                              }
                            />
                          )}
                        </span>
                      </Link>
                    </div>
                    <Link to={`/Evenements/${rowsEvents[i]._id}`}>
                      <div className="evenement__right">
                        <div className="right__date">
                          <div>
                            <img
                              className="lazyload"
                              src={calendar}
                              alt=""
                              srcSet=""
                            />
                            {moment(rowsEvents[i].date).format("DD-MM-YYYY")}
                          </div>
                          <div>
                            <img
                              className="lazyload"
                              src={time_clock_circle}
                              alt=""
                            />
                            {moment(rowsEvents[i].date).format("LT")}
                          </div>
                        </div>
                        <div className="righ__desctiption">
                          {langue === "ar"
                            ? rowsEvents[i].titleAR
                            : langue === "fr"
                            ? rowsEvents[i].titleFR
                            : rowsEvents[i].titleEN}
                        </div>
                        <div className="right__footer">
                          {langue === "ar"
                            ? rowsEvents[i].emplacementAR
                            : langue === "fr"
                            ? rowsEvents[i].emplacementFR
                            : rowsEvents[i].emplacementEN}
                        </div>
                      </div>
                    </Link>
                  </div>
                ))}
              </div>
              <div className="evenement__calendar">
                <Scheduler
                  rtlEnabled={langue === "ar"}
                  views={[]}
                  onAppointmentDblClick={(e) => {
                    history.push(`/Evenements/${e.appointmentData.id}`);
                  }}
                  appointmentTooltipRender={renderAppointmentTooltip}
                  timeZone="France/Paris"
                  dataSource={eventData}
                  defaultCurrentView="month"
                  defaultCurrentDate={currentDate}
                  height={600}
                  firstDayOfWeek={1}
                  startDayHour={8}
                  endDayHour={18}
                ></Scheduler>
              </div>
            </div>
          </div>
        </div>
      ) : null}
    </>
  );
}
export default EventsComponent;
