import React, { useState } from "react";
import "./Slider.css";
import BtnSlider from "./BtnSlider";
import { dataSlider, bottomSLider } from "./dataSlider";
import Image from "../../shared/Image";
import PropTypes from "prop-types";

import { Link } from "react-router-dom";
import { noImage } from "src/assets/images";

function Slider2(props) {
  const alt = props.slider;
  const rows = props.rows;
  const [slideIndex, setSlideIndex] = useState(1);
  const width = Number(props.width);
  const height = Number(props.height);
  const langue = localStorage.getItem("language");
  const nextSlide = () => {
    if (slideIndex !== rows.length) {
      setSlideIndex(slideIndex + 1);
    } else if (slideIndex === rows.length) {
      setSlideIndex(1);
    }
  };

  const prevSlide = () => {
    if (slideIndex !== 1) {
      setSlideIndex(slideIndex - 1);
    } else if (slideIndex === 1) {
      setSlideIndex(rows.length);
    }
  };

  const moveDot = (index) => {
    setSlideIndex(index);
  };

  return (
    <div
      className="container-slider"
      style={{
        width: width,
        height: height,
      }}
    >
      {props.rows.map((obj, index) => {
        return (
          <div
            key={index}
            className={slideIndex === index + 1 ? "slide active-anim" : "slide"}
          >
            <Link to={`/detail/${props.rows[slideIndex - 1]._id}`}>
              {langue === "fr" ? (
                <img
                  style={{
                    opacity: 0.4,
                  }}
                  width={width}
                  height={height}
                  className="lazyload"
                  src={
                    obj?.photos?.length > 0
                      ? obj?.photos[0].downloadUrl
                      : noImage
                  }
                  alt="Search Archeive ICon"
                  srcSet=""
                />
              ) : langue === "ar" ? (
                <img
                  style={{
                    opacity: 0.4,
                  }}
                  width={width}
                  height={height}
                  className="lazyload"
                  src={
                    obj?.photosAR?.length > 0
                      ? obj?.photosAR[0].downloadUrl
                      : alt
                  }
                  alt="Search Archeive ICon"
                  srcSet=""
                />
              ) : (
                <img
                  style={{
                    opacity: 0.4,
                  }}
                  width={width}
                  height={height}
                  className="lazyload"
                  src={
                    obj?.photosEN?.length > 0
                      ? obj?.photosEN[0].downloadUrl
                      : alt
                  }
                  alt="Search Archeive ICon"
                  srcSet=""
                />
              )}

              {props.render(obj)}
            </Link>
          </div>
        );
      })}
      <BtnSlider moveSlide={nextSlide} direction={"next"} />
      <BtnSlider moveSlide={prevSlide} direction={"prev"} />
      {props?.showDots && (
        <div className="container-dots">
          {props.rows.map((item, index) => (
            <div
              onClick={() => moveDot(index + 1)}
              className={slideIndex === index + 1 ? "dot active" : "dot"}
            ></div>
          ))}
        </div>
      )}
      {!props?.showDots && (
        <div className="content__sliderBottom">
          {props.rows.map((item, index) =>
            props.bottomrender(item, index, slideIndex)
          )}
        </div>
      )}
    </div>
  );
}

Slider2.propTypes = {
  rows: PropTypes.any,
  label: PropTypes.string,
  height: PropTypes.number,
  width: PropTypes.number,
  render: PropTypes.func,
  bottomrender: PropTypes.func,
  showDots: PropTypes.bool,
};
export default Slider2;
