import {
  slider1,
  calendar__bttom,
  header__image__mobile,
} from "src/assets/images";

import Slider2 from "../shared/Slider/slider2";
import { i18n } from "src/i18n";
import { useEffect } from "react";
// publications
import actionsPublication from "src/modules/publication/list/publicationListActions";
import selectorsPublication from "src/modules/publication/list/publicationListSelectors";
import { Link } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";

// archives
import Archives from "src/view/Archive/Archives";

// events
import actionsEvents from "src/modules/evenement/list/evenementListActions";
// calendar
import "src/view/shared/styles/dx.generic.tadeco-scheme-ftdes.css";
import DashboardComponent from "src/view/Home/DashboardComponent";
import PublicationsComponent from "./PublicationsComponent";
import EventsComponent from "src/view/Home/EventsComponent";

// stats
import { noImage } from "src/assets/images";
function Home() {
  const dispatch = useDispatch();
  const langue = localStorage.getItem("language");
  var localization = require("devextreme/localization");
  localization.locale(langue);

  useEffect(() => {
    dispatch(actionsPublication.doFetchValidePublication(4));
    dispatch(actionsEvents.doFetch());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const rowsPublication = useSelector(selectorsPublication.selectRows);

  const Substring = (text, limit) => {
    if (text) {
      if (text.length > limit) {
        return text.substring(0, limit) + "...";
      } else return text;
    } else return text;
  };

  const sliderTop = (obj) => {
    return (
      <div className="content__sliderTop">
        <div className="textContent">
          <div
            className="horizontalPlayerTitle ow"
            style={{
              color: "white",
              textShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
              fontFamily: "Bebas Neue",
              fontStyle: "normal",
              fontWeight: 400,
              fontSize: "47px",
              lineHeight: "40px",
            }}
          >
            {langue === "ar"
              ? obj?.titleAR
              : langue === "fr"
              ? obj?.titleFR
              : obj?.titleEN}
          </div>
        </div>
      </div>
    );
  };

  const sliderBottom = (obj, index, slideIndex) => {
    return (
      <div className={slideIndex === index + 1 ? "active__slideBottom" : ""}>
        <span
          className=" lazy-load-image-background blur lazy-load-image-loaded"
          style={{ color: "transparent", display: "block" }}
        >
          <img
            style={{ width: 250, height: 100 }}
            alt={noImage}
            src={
              langue === "fr"
                ? obj?.photos?.length > 0
                  ? obj?.photos[0].downloadUrl
                  : noImage
                : langue === "ar"
                ? obj?.photosAR?.length > 0
                  ? obj?.photosAR[0].downloadUrl
                  : noImage
                : obj?.photosEN?.length > 0
                ? obj?.photosEN[0].downloadUrl
                : noImage
            }
          />
          <p
            className={
              slideIndex === index + 1 ? "text__p text__gras" : "text__p"
            }
          >
            {langue === "ar"
              ? Substring(obj?.titleAR, 30)
              : langue === "fr"
              ? Substring(obj?.titleFR, 30)
              : Substring(obj?.titleEN, 30)}
          </p>
        </span>
      </div>
    );
  };

  return (
    <div className="app">
      {/* Styling the Header */}

      <div className="app__calendar">
        <Link to="/Evenements">
          <img src={calendar__bttom} alt="" />
        </Link>
      </div>
      <div className="app__header">
        <div className="header__content" style={{ height: 549 }}>
          <div className="content">
            <div className="content__imagePrincipale">
              <Slider2
                rows={rowsPublication}
                height={549}
                width={1170}
                label="slider"
                render={sliderTop}
                showDots={false}
                bottomrender={sliderBottom}
              />

              <img
                className="lazyload"
                src={header__image__mobile}
                id="image__mobile"
                alt="Header "
              />
            </div>
          </div>
        </div>
      </div>

      {/* Styling the statestique */}
      <div className="app__statestique">
        <div className="statestique">
          <div className="archieve__header">
            <h2>{i18n("menu.Tableau")}</h2>
            <div className="satestique__bar" />
          </div>
          <div style={{ marginTop: 20 }}>
            <DashboardComponent />
          </div>
        </div>
      </div>
      {/* Styling the archeive */}
      <div
        className="archieve__header"
        style={{
          maxWidth: "1170px",
          marginLeft: "210px",
          marginRight: "210px",
        }}
      >
        <h2>{i18n("common.Archives")}</h2>
        <div className="publication__bar" />
      </div>
      <div className="archeive" style={{ maxWidth: "100%" }}>
        <div style={{ marginTop: 43 }}>
          <Archives acceuil={true} />
        </div>
      </div>
      {/* Stying the Publication */}
      <PublicationsComponent />

      {/* Glossaire */}

      <div className="Glossaire">
        <h5>{i18n("menu.Glossaire")}</h5>
        <Link to="/Glossaire">
          <div className="global__button">{i18n("common.discover")}</div>
        </Link>
      </div>

      {/* Styling the Evenement */}
      <EventsComponent />
    </div>
  );
}

export default Home;
